<template>
    <div class="container">
        <TodoItem
            :todos="incompleteTodos"
            :setCurrentTodo="setCurrentTodo"
            :toggleTodoCompletion="toggleTodoCompletion"
        />
        <button ref="showIncompleteButton" :class="['btn btn-outline-secondary mb-3',{'mt-3':incompleteTodos.length>0}]" @click="toggleShowCompleted($event)">
            <Transition name="chevron-transition">
                <i :class="chevronClass"></i>
            </Transition>
             Completed {{ completedCount }}
        </button>
        <Transition>
            <TodoItem
                v-show="showCompleted"
                :todos="completedTodos"
                :setCurrentTodo="setCurrentTodo"
                :toggleTodoCompletion="toggleTodoCompletion"
            />
        </Transition>
    </div>
    <TodoModal
        v-if="currentTodo !== null"
        modalId="editTodoModal"
        :isNew="false"
        :todo="currentTodo"
        :deleteTodo="deleteTodo"
        :toggleTodoCompletion="toggleTodoCompletion"
    />
    <div class="fixed-bottom" style="height: 70px;">
        <div class="container" style="height: 50px;">
            <div class="d-flex justify-content-end">
            <Transition>
                <input
                    v-show="showInput"
                    ref="todoInput"
                    list="itemOptions"
                    style="height: 45px;"
                    type="text"
                    class="form-control bg-secondary-subtle"
                    placeholder="Add a new item..."
                    v-model="newTodo"
                    @keyup.enter="addTodo"
                    @focusout="showInput=false"
                />
            </Transition>
            <datalist id="itemOptions">
                <option v-for="(name, index) in uniqueItems" :key="index" :value="name" >
                </option>
            </datalist>
            <button v-show="!showInput" @click="showInput = !showInput" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button"><i class="bi bi-plus-lg"></i></button>
            </div>
        </div>
    </div>
</template>

<script>
import TodoItem from './TodoItem.vue';
import TodoModal from './TodoModal.vue';
const blankTodoObject = {name:"",done:false,id:0};

export default {
    data() {
        return {
            newTodo: '',
            todos: JSON.parse(localStorage.getItem('todoItems')) || [],
            showCompleted: false,
            currentTodo: blankTodoObject,
            showInput:false
        };
    },
    components: {
        TodoItem,
        TodoModal
    },
    computed: {
        sortedTodos() {
            return [...this.todos].sort((a,b)=>b.id - a.id);
        },
        completedTodos() {
          return this.sortedTodos.filter(todo => todo.done);
        },
        incompleteTodos() {
          return this.sortedTodos.filter(todo => !todo.done);
        },
        completedCount() {
          return this.completedTodos.length;
        },
        uniqueItems(){
            return [...new Set(this.todos.map(todo => todo.name))].sort()
        },
        chevronClass(){
            return this.showCompleted ? "bi bi-chevron-down" : "bi bi-chevron-right";
        }
      },
    methods: {
        capitalizeFirstLetter(string) {
            string = string.trim();
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        removeFocus(event) {
            setTimeout(()=>{
              event.target.style.outline = 'none';
              event.target.style.boxShadow = 'none';
              this.$refs.showIncompleteButton.blur();
            },250)
        },
        toggleShowCompleted(event){
            this.showCompleted = !this.showCompleted;
            this.removeFocus(event);
        },
        addTodo() {
            if (this.newTodo.trim()) {
                this.todos.push({ id: Date.now(), name: this.capitalizeFirstLetter(this.newTodo), done: false });
                this.newTodo = '';
                this.saveTodos();
            }
        },
        toggleTodoCompletion(todo) {
          // Temporarily mark the todo as not completed
          const todoDone = todo.done;
          todo.done = todoDone;
          this.saveTodos();
        },
        setCurrentTodo(todo){
            this.currentTodo = todo;
        },
        deleteTodo(id) {
            this.todos = this.todos.filter(todo => todo.id !== id );
            this.saveTodos();
        },
        saveTodos() {
            localStorage.setItem('todoItems', JSON.stringify(this.todos));
        }
    },
    watch: {
        showInput(newValue){
            if (newValue) {
                this.$nextTick(()=>{
                    this.$refs.todoInput.focus();
                });
            }
        }
    }
};
</script>

<style>
.btn {
  z-index: 1;
}
.fixed-bottom {
  opacity: .95;
}
.v-enter-active {
    transition: opacity 0.5s ease;
}
.v-leave-active {
    transition: opacity 0.5s ease;
}
.v-enter-from {
    opacity: 0;
}
.v-leave-to {
    opacity: 0;
}
.container {
    overflow-x: hidden;
}
</style>
