<template>
    <div class="container">
        <textarea
            v-if="editMode"
            ref="textInput"
            class="form-control p-2"
            rows="1"
            v-model="text"
            @input="autoResize"
            :style="{height:textareaHeight}">
        </textarea>
        <div v-else v-html="parsedText"></div>
    </div>
    <div class="fixed-bottom" style="height: 70px;">
        <div class="container" style="height: 50px;">
            <div class="d-flex justify-content-end">
            <button @click="editMode = !editMode" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button">
                <Transition mode="in-out">
                    <i v-if="editMode" class="bi bi-eye-fill"></i>
                    <i v-else class="bi bi-pencil-fill  "></i>
                </Transition>
            </button>
            </div>
        </div>
    </div>
</template>

<script>
let marked = require('marked');

export default {
    data() {
        return {
            editMode:false,
            textareaHeight: "auto",
            text:""
        };
    },
    components: {
    },
    computed: {
        parsedText() {
            return marked.parse(this.text);
        }
    },
    methods: {
        autoResize(event) {
            this.textareaHeight = 'auto'; // Reset height to auto to calculate new height
            // Use a timeout to allow the browser to reflow
            this.$nextTick(() => {
                this.textareaHeight = event.target.scrollHeight + 'px'; // Set height to scrollHeight
            });
        }
    },
    watch: {
        editMode(newValue){
            if (newValue) {
                this.$nextTick(()=>{
                    this.$refs.textInput.focus();
                });
            }
        }
    }
};
</script>
<style>
.btn {
    overflow-y: hidden;
}
</style>